.container {
  max-width: 100%;
  padding: 0 20px;
}

@media screen and (min-width: 380px) {
  .container {
    max-width: 380px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    margin: 0 auto;
    padding: 0 75px;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 1024px;
    margin: 0 auto;
    padding: 0 71px;
  }
}

.footer {
  color: #545454;
  background-color: #f7f7f7;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 16.41px;
}

@media screen and (min-width: 768px) {
  .footer {
    font-size: 16px;
  }
}

.footer__wrapper {
  flex-direction: column;
  justify-content: center;
  gap: 9px;
  height: 100px;
  padding-left: 48px;
  padding-right: 48px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .footer__wrapper {
    flex-direction: row;
    align-items: center;
    height: 79px;
  }
}

.footer__wrapper p {
  justify-content: center;
  display: flex;
}

.footer__image {
  width: 14px;
  margin-left: 10px;
  margin-right: 10px;
}

.footer__year {
  margin-left: 8px;
  margin-right: 4px;
}

body {
  box-sizing: border-box;
  color: #000;
  background-color: #fff;
  font-family: Roboto, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

button {
  cursor: pointer;
}

li {
  list-style: none;
}

